@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=JetBrains+Mono:wght@400;500;600;700&family=Inter:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        --background: 0 0% 2%;
        --foreground: 0 0% 98%;
        --card: 0 0% 4%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 4%;
        --popover-foreground: 0 0% 98%;
        --primary: 184 100% 50%;
        --primary-foreground: 0 0% 0%;
        --secondary: 348 100% 50%;
        --secondary-foreground: 0 0% 100%;
        --muted: 0 0% 15%;
        --muted-foreground: 0 0% 64%;
        --accent: 100 100% 54%;
        --accent-foreground: 0 0% 0%;
        --destructive: 348 100% 50%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 15%;
        --input: 0 0% 15%;
        --ring: 184 100% 50%;
        --radius: 0rem;
        --chart-1: 184 100% 50%;
        --chart-2: 348 100% 50%;
        --chart-3: 100 100% 54%;
        --chart-4: 43 100% 50%;
        --chart-5: 280 100% 50%;
    }
}

@layer base {
    * {
        @apply border-border;
        box-sizing: border-box;
    }
    
    body {
        @apply bg-background text-foreground;
        font-family: 'Inter', sans-serif;
        margin: 0;
        padding: 0;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        overflow-x: hidden;
    }
    
    h1, h2, h3, h4, h5, h6 {
        font-family: 'Space Grotesk', sans-serif;
    }
    
    code, pre, .font-mono {
        font-family: 'JetBrains Mono', monospace;
    }
}

/* Scanline Effect - Disabled on mobile for performance */
@layer base {
    @media (min-width: 768px) {
        body::before {
            content: '';
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            z-index: 9999;
            background: repeating-linear-gradient(
                0deg,
                rgba(0, 0, 0, 0.1) 0px,
                rgba(0, 0, 0, 0) 1px,
                rgba(0, 0, 0, 0) 2px
            );
            will-change: transform;
        }
    }
}

/* Grid Background */
.grid-background {
    background-image: 
        linear-gradient(rgba(0, 240, 255, 0.03) 1px, transparent 1px),
        linear-gradient(90deg, rgba(0, 240, 255, 0.03) 1px, transparent 1px);
    background-size: 50px 50px;
}

/* Neon Glow Effect */
.neon-glow {
    box-shadow: 0 0 10px rgba(0, 240, 255, 0.5), 0 0 20px rgba(0, 240, 255, 0.3);
}

.neon-glow-red {
    box-shadow: 0 0 10px rgba(255, 0, 60, 0.5), 0 0 20px rgba(255, 0, 60, 0.3);
}

/* Reduce glow on mobile for performance */
@media (max-width: 768px) {
    .neon-glow {
        box-shadow: 0 0 8px rgba(0, 240, 255, 0.4);
    }
}

/* Glassmorphism */
.glass {
    backdrop-filter: blur(24px);
    background: rgba(0, 0, 0, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.glass-panel {
    backdrop-filter: blur(12px);
    background: rgba(0, 0, 0, 0.4);
    border: 1px solid rgba(255, 255, 255, 0.05);
}

/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
    background: rgba(0, 240, 255, 0.5);
    border-radius: 0;
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 240, 255, 0.7);
}

/* Cyber Button Animation */
.cyber-btn {
    position: relative;
    transition: all 0.3s ease;
}

.cyber-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: rgba(0, 240, 255, 0.1);
    transition: width 0.3s ease;
}

.cyber-btn:hover::before {
    width: 100%;
}

/* Status Indicators */
.status-critical {
    color: #ff003c;
    text-shadow: 0 0 10px rgba(255, 0, 60, 0.5);
}

.status-high {
    color: #ffbd00;
    text-shadow: 0 0 10px rgba(255, 189, 0, 0.5);
}

.status-medium {
    color: #00f0ff;
    text-shadow: 0 0 10px rgba(0, 240, 255, 0.5);
}

.status-low {
    color: #39ff14;
    text-shadow: 0 0 10px rgba(57, 255, 20, 0.5);
}

/* Progress Bar */
.progress-bar {
    height: 4px;
    background: rgba(0, 240, 255, 0.2);
    position: relative;
    overflow: hidden;
}

.progress-bar::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: linear-gradient(90deg, transparent, #00f0ff, transparent);
    animation: progress-scan 2s linear infinite;
}

@keyframes progress-scan {
    0% {
        left: -100%;
        width: 100%;
    }
    100% {
        left: 100%;
        width: 100%;
    }
}

/* Fade in animation */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in {
    animation: fadeIn 0.6s ease-out;
}

/* Slide in animation */
@keyframes slideInFromTop {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-in {
    animation: slideInFromTop 0.4s ease-out;
}

/* Pulse glow animation */
@keyframes pulseGlow {
    0%, 100% {
        box-shadow: 0 0 10px rgba(0, 240, 255, 0.3);
    }
    50% {
        box-shadow: 0 0 20px rgba(0, 240, 255, 0.6);
    }
}

.animate-pulse-glow {
    animation: pulseGlow 2s ease-in-out infinite;
}

/* Data Grid */
.data-grid {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.4);
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }

    [data-debug-wrapper="true"] > * {
        margin-left: inherit;
        margin-right: inherit;
        margin-top: inherit;
        margin-bottom: inherit;
        padding-left: inherit;
        padding-right: inherit;
        padding-top: inherit;
        padding-bottom: inherit;
        column-gap: inherit;
        row-gap: inherit;
        gap: inherit;
        border-left-width: inherit;
        border-right-width: inherit;
        border-top-width: inherit;
        border-bottom-width: inherit;
        border-left-style: inherit;
        border-right-style: inherit;
        border-top-style: inherit;
        border-bottom-style: inherit;
        border-left-color: inherit;
        border-right-color: inherit;
        border-top-color: inherit;
        border-bottom-color: inherit;
    }
}